*{
    margin: 0px;
    padding: 0px;
}
footer{
    background-color: #F4CE14;
    padding: 1rem;
    border-radius: 16px;
}
.footer{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap:1rem;
    margin: 3rem;
    list-style: none;
    text-decoration: none;
}
li{
    list-style: none;
    text-decoration: none;
    color: black;
}
.link1{
    text-decoration: none;
    color: black;
}
.img-footer > img{
    display: block;
    margin: auto;
    width:4rem;
}
.footer-header{
    margin-bottom: 1rem;
}
.footer-links{
    display: flex;
    flex-direction:column;
    list-style: none;
    text-decoration: none;
}
@media only screen and (max-width:780px)
{
  footer{
    background-color: #F4CE14;
    padding: 1rem;
    border-radius: 16px;
    width: 52rem;
}
}
@media only screen and (max-width:420px)
{
  footer{
    width: 57rem;
}
}