*{
  margin: 0px;
  padding: 0px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
/* Hero Section Style Code */
.Profile{
  display: flex;
  align-items: center;
  background-color:  #495E57;
  height: 25rem;
  margin-top: 3rem;
  width: 100%;
}
.data{
  margin: auto 3rem;
  /* background-color:  #495E57; */
}
.profile-img{
  display: block;
  margin: auto;
  padding: 5rem;
}
.profile-img > img{
  width: 300px;
  border-radius: 16px;
}
.info{
  padding: 0.4rem;
}
.description{
  color: white;
}
.btn{
  padding: 0.3rem;
  border-radius: 16px;
  margin: 0rem 0rem 0rem 0.4rem;
}
@media only screen and (max-width:840px)
{
  .Profile{
    width: 54rem;
  }
}
@media only screen and (max-width:414px)
{
  .Profile{
    display: flex;
    align-items: center;
    background-color:  #495E57;
    height: 35rem;
    margin-top: 3rem;
    width: 55rem;
  }
}