*{
    margin:0px;
    padding: 0px;
}
/* Special section style code */
.special{
    margin-top: 15rem;
}
.heading{
    margin-left: 4rem;
}
.cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1rem;
    margin: 3rem;
    width: 90%;
}
.card img{
    display: block;
    margin: auto;
    border-radius: 16px 16px 0 0;
    width: 100%;
    max-height: 30%;
}
.card{
    background-color: #edefee;
    padding:auto 1rem;
    border-radius: 2rem;
    height: 37rem;
}
.dish-info{
    text-align:left;
}
.title{
    display: flex;
    justify-content: space-between;
    margin: auto 1rem;
}
.card p{
    margin: 5rem 1rem;
    text-align: justify;
}
.specials-order-btn{
    margin: auto 1rem;

}
.btn1{
    background-color: #F4CE14;
    padding: 0.7rem;
    font-weight: bold;
    font-size: medium;
    text-align: center;
    max-width: 10%;
    margin: 1rem 5rem;
    border-radius: 16px;
    border: 2px solid black;
}
@media only screen and (max-width:1450px)
  {
    html{
        scroll-behavior: smooth;
    }
    .special{
        margin-top: 15rem;
    }
    .cards{
        display: grid;
        grid-template-columns:1fr;
        grid-gap: 3rem;
        margin: auto;
        width:500px;
    }
    .card img{
        border-radius: 16px 16px 0 0;
        width: 470px;
        max-height: 30%;
        object-fit: cover;
    }
    .card{
        justify-content: center;
        align-items: center;
    }
    .btn1{
        max-width: 15%;
    }
  }
  @media only screen and (max-width:490px)
{
    .special{
        margin-top: 15rem;
        width: 55rem;
    }
}